"use client";

import { useEffect, useState } from 'react';
import { useSearchParams } from 'next/navigation';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';

interface Order {
  id: string;
  restaurantName: string;
  items: Array<{
    name: string;
    quantity: number;
    price: number;
  }>;
  total: number;
  status: 'preparing' | 'on-the-way' | 'delivered' | 'cancelled';
  orderDate: string;
  estimatedDelivery?: string;
}

// Mock orders data
const mockOrders: Order[] = [
  {
    id: '1001',
    restaurantName: 'Pizza Palace',
    items: [
      { name: 'Margherita Pizza', quantity: 1, price: 14.99 },
      { name: 'Garlic Bread', quantity: 1, price: 6.99 },
    ],
    total: 26.97,
    status: 'on-the-way',
    orderDate: '2024-01-15T18:30:00Z',
    estimatedDelivery: '19:15',
  },
  {
    id: '1002',
    restaurantName: 'Burger Barn',
    items: [
      { name: 'Classic Cheeseburger', quantity: 2, price: 12.99 },
      { name: 'French Fries', quantity: 1, price: 4.99 },
    ],
    total: 35.95,
    status: 'delivered',
    orderDate: '2024-01-14T12:15:00Z',
  },
  {
    id: '1003',
    restaurantName: 'Sushi Zen',
    items: [
      { name: 'California Roll', quantity: 2, price: 8.99 },
      { name: 'Salmon Avocado Roll', quantity: 1, price: 10.99 },
    ],
    total: 33.95,
    status: 'delivered',
    orderDate: '2024-01-13T19:45:00Z',
  },
];

function getStatusColor(status: Order['status']) {
  switch (status) {
    case 'preparing':
      return 'bg-yellow-100 text-yellow-800';
    case 'on-the-way':
      return 'bg-blue-100 text-blue-800';
    case 'delivered':
      return 'bg-green-100 text-green-800';
    case 'cancelled':
      return 'bg-red-100 text-red-800';
    default:
      return 'bg-gray-100 text-gray-800';
  }
}

function getStatusText(status: Order['status']) {
  switch (status) {
    case 'preparing':
      return 'Preparing';
    case 'on-the-way':
      return 'On the Way';
    case 'delivered':
      return 'Delivered';
    case 'cancelled':
      return 'Cancelled';
    default:
      return status;
  }
}

export default function OrdersPage() {
  const searchParams = useSearchParams();
  const [orders, setOrders] = useState<Order[]>(mockOrders);
  const [showSuccess, setShowSuccess] = useState(false);

  useEffect(() => {
    if (searchParams.get('success') === 'true') {
      setShowSuccess(true);
      // Add a new order to simulate successful payment
      const newOrder: Order = {
        id: Date.now().toString(),
        restaurantName: 'Recent Order',
        items: [{ name: 'Your Order', quantity: 1, price: 0 }],
        total: 0,
        status: 'preparing',
        orderDate: new Date().toISOString(),
        estimatedDelivery: new Date(Date.now() + 30 * 60000).toLocaleTimeString('en-US', {
          hour: '2-digit',
          minute: '2-digit',
        }),
      };
      setOrders(prev => [newOrder, ...prev]);
      
      // Hide success message after 5 seconds
      setTimeout(() => setShowSuccess(false), 5000);
    }
  }, [searchParams]);

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-bold text-gray-900">Your Orders</h1>

      {showSuccess && (
        <div className="bg-green-50 border border-green-200 text-green-800 px-4 py-3 rounded-md">
          <p className="font-semibold">Payment Successful!</p>
          <p className="text-sm">Your order has been placed and is being prepared.</p>
        </div>
      )}

      {orders.length === 0 ? (
        <div className="text-center py-12">
          <h2 className="text-xl font-semibold text-gray-900 mb-2">No orders yet</h2>
          <p className="text-gray-600">When you place orders, they'll appear here</p>
        </div>
      ) : (
        <div className="space-y-4">
          {orders.map((order) => (
            <Card key={order.id}>
              <CardHeader className="pb-3">
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="text-lg">{order.restaurantName}</CardTitle>
                    <p className="text-sm text-gray-600">
                      Order #{order.id} â€¢ {formatDate(order.orderDate)}
                    </p>
                  </div>
                  <Badge className={getStatusColor(order.status)}>
                    {getStatusText(order.status)}
                  </Badge>
                </div>
              </CardHeader>
              <CardContent className="pt-0">
                <div className="space-y-3">
                  {/* Order Items */}
                  <div className="space-y-1">
                    {order.items.map((item, index) => (
                      <div key={index} className="flex justify-between text-sm">
                        <span>
                          {item.quantity}x {item.name}
                        </span>
                        <span>${(item.price * item.quantity).toFixed(2)}</span>
                      </div>
                    ))}
                  </div>

                  {/* Total */}
                  <div className="flex justify-between font-semibold pt-2 border-t border-gray-200">
                    <span>Total</span>
                    <span>${order.total.toFixed(2)}</span>
                  </div>

                  {/* Estimated Delivery */}
                  {order.status === 'preparing' || order.status === 'on-the-way' ? (
                    <div className="bg-blue-50 p-3 rounded-md">
                      <p className="text-sm text-blue-800">
                        {order.status === 'preparing' 
                          ? 'Your order is being prepared'
                          : 'Your order is on the way'
                        }
                        {order.estimatedDelivery && (
                          <span className="block font-semibold">
                            Estimated delivery: {order.estimatedDelivery}
                          </span>
                        )}
                      </p>
                    </div>
                  ) : null}
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
